import Vue from 'vue';
import Vuetify from 'vuetify/lib';
import colors from 'vuetify/lib/util/colors'

Vue.use(Vuetify);

export default new Vuetify({
    theme: {
        themes: {
            light: {
                primary: colors.teal.darken2,
                secondary: colors.grey.darken3,
                accent: colors.red.darken3
            }
        }
    },
    icons: {
        iconfont: 'mdiSvg'
    }
});
